<template>
         <div id="pay" style="margin-top: 80px;">
             <el-row>
                 <el-col :span="16" :offset="4">
                   <router-link to="/">
                    <el-page-header  content="交费续费"></el-page-header>
                    </router-link>
                 </el-col>
             </el-row>

          <div style="background-color: #fafafa;">

          <el-row >
            <el-col :span="16" :offset="4">
             <el-steps :active="active" finish-status="success" >
               <el-step title="缴费与充值"></el-step>
               <el-step title="交易明细"></el-step>
               <el-step title="交费成功"></el-step>
             </el-steps>

            </el-col>
          </el-row>
<el-main>
        <!-- 交费查询 -->
          <el-row>
            <el-col :span="16" :offset="4">
                <div class="type-search">
                  <span style="margin-right: 10px;">选择交费类型:</span>
                   <el-select v-model="value" placeholder="请选择">
                      <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                    <el-button type="primary" @click='search()'>查询</el-button>
                    <div id="searchResult"></div>
                </div>
            </el-col>
          </el-row>

          <!-- 查询表格 -->
        <el-row>
            <el-col :span="16" :offset="4">
               <el-table
                  :data="tableData"
                  border
                  show-summary
                  style="width: 100%">
                  <el-table-column
                    prop="year"
                    label="年度"
                    width="180">
                  </el-table-column>
                  <el-table-column
                    prop="category"
                    label="名称">
                  </el-table-column>
                  <el-table-column
                    prop="pay"
                    sortable
                    label="已缴金额">
                  </el-table-column>
                  <el-table-column
                    prop="unpay"
                    sortable
                    label="应缴金额">
                  </el-table-column>
                </el-table>
                <el-button type="danger" plain style="float: right; padding: 10px 70px 10px 70px;">结算</el-button>


            </el-col>
            </el-row>
          </el-main>
          </div>

          </div>
</template>


<script>
  import Vuex from "vuex";
  import axios from "axios";
export default {
  name: 'Tuition',
  data () {
    return {
      active: 1,
      value: '',
      options: [{
        value: '0',
        label: '未缴学费'
      }, {
        value: '1',
        label: '以缴学费'
      }],

        tableData: [],
        loading:false
    }
  },
  created() {
    let  instance = axios.create({
      baseURL:'http://127.0.0.1/myselfApi',
      timeout:2000
    })
    instance.get('home/tuition.php').then(res=>{
      this.tableData = res.data
    })

    // 加载后的请求拦截器

  },

  methods: {
    next () {
      if (this.active++ > 2) this.active = 1
      this.$message({
        message: '提交申请表成功,请等待审批',
        type: 'success'
      })
    },
    tableRowClassName ({ row, rowIndex }) {
      if (rowIndex === 1) {
        return 'warning-row'
      } else if (rowIndex === 3) {
        return 'success-row'
      }
      return ''
    },
    search() {
       let instance = axios.create({
        baseURL:'http://127.0.0.1/myselfApi',
        timeout:2000
      })

      instance.get('home/tuitionA.php',{
        params:{
          state:this.value
        }
      }).then((res)=>{
        this.tableData = res.data
      })
    },


}}
</script>

<style scoped>
  .type-search{
    text-align: center;
  }
  .el-button:hover{
    cursor: pointer;
  }
    .el-table .warning-row {
      background: oldlace;
    }

    .el-table .success-row {
      background: #f0f9eb;
    }
</style>
